.contenedor-general-navtoggler{
    position:absolute;
    display:flex;
    padding-top:50px;
    color:white;
    width: 100%;
    
}


/* Hamburger */
.ham-toggler-div{
    margin-left:25px;
    margin-top:7px;
    color:rgb(171, 171, 171);
    z-index: 5;
}

.buscador-inicio-nav{
    margin-top:2px;
    margin-left:18px;
    
}

.buscador-inicio-nav .form-control{
    color:White;
    padding-top:3px;
    padding-bottom:3px;
    padding-right:60px;
    border-radius: 20px;
    background-color: #383838;
    border-color: #383838;
}
.buscador-inicio-nav .input-group-text{
    color:white;
    border-radius: 20px;
    background-color: #383838;
    border-color: transparent;
}





/* DESACTIVADO */
.div-oculto-toggler{
    display:none;
}

.div-oculto-toggler .links{
    display:none;
}

.overlay-nav-toggler{
    display:none;
}






/* ACTIVADO */
.div-oculto-toggler-activo{
    position:fixed;
    display:block;
    top:13vh;
    width: 100vw;
    height: 87vh;
    
    background-color:#070707;
    z-index:9;
}


.div-oculto-toggler-activo .links{
    width: 100%;
    display:block;
    float:left;
   /*  margin-left:20px; */
    margin-top:40px;
}

.overlay-nav-toggler-activo{
    position: absolute;
    top:0px;
    width: 100%;
    height: 100vh;
    background-color:rgba(0, 0, 0, 0.735);
    z-index: 8;
}








.links h2{
   text-align: center;
   
    
    margin-top:-10px;
    margin-bottom: 0px;
}
.links h3{
    font-size: 20px;
    margin-left:20px;
    margin-bottom: 20px;
}

.links .cruz{
    position: absolute;
    
    right:20px;
    top:38px;
}
